.hero {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100vh;
  background-color: #3aae84;
  color: #fff;
  margin-bottom: 4rem;
}
.hero__text {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.hero__title {
  max-width: 12em;
  font-size: 10vw;
  font-weight: 900;
  text-align: center;
}
.hero__img--one {
  display: none;
}
.hero__img {
  display: none;
}
.hero__content {
  max-width: 20em;
  margin: 1em 0 2.5em 0;
  font-size: 4vw;
  text-align: center;
}
/* @ Screen 400 */

@media screen and (max-width: 400px) {
  .hero__title {
    margin: 0 0.8em;
  }
  .hero__content {
    margin-left: 2em;
    margin-right: 2em;
  }
}

/* @ Screen 540px */

@media screen and (min-width: 540px) {
  .hero {
    /* max-width: 500px; */
    background-color: #ffffff;
    color: #000;
    height: 100vh;
    text-align: center;
    justify-content: center;
    align-items: center;
    margin: 10em 2em;
  }

  .hero__text {
    /* max-width: 400px; */
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .hero__img--one {
    display: block;
  }
  .hero__img--one div img {
    object-fit: contain;
    max-width: 100%;
  }
  .hero__title {
    margin-top: 1em;
    max-width: 19em;
    font-size: 40px;
    font-weight: 900;
    text-align: center;
  }
  .hero__content {
    max-width: 33em;
    font-size: 20px;
    text-align: center;
  }
}
/* @ Screen 768px */
@media screen and (min-width: 768px) {
  .hero {
    background-color: #ffffff;
    color: #000;
    justify-content: center;
  }

  .hero__img--one {
    display: block;
  }
  .hero__title {
    max-width: 19em;
    font-size: 5.5vw;
    font-weight: 900;
    text-align: center;
  }
  .hero__content {
    max-width: 33em;
    margin: 1.2em 0 3em 0;
    font-size: 20px;
    text-align: center;
  }
}

/* @ Screen 1004px */

@media screen and (min-width: 1004px) {
  .hero {
    margin-left: 5em;
    margin-right: 5em;
    margin-top: 2.9em;
    background-color: #ffffff;
    color: #000;
    height: auto;
    align-items: flex-start;
    flex-direction: row;
    text-align: start;
    align-items: center;
    justify-content: space-around;
  }
  .hero__text {
    align-items: flex-start;
    justify-content: center;
    text-align: center;
  }
  .hero__title {
    width: 10em;
    font-size: 4.5vw;
    text-align: left;
  }

  .hero__content {
    width: 40em;
    text-align: left;
    margin-bottom: 3em;
  }
  /* this is not working */
  .hero__btn button {
    display: flex;
    justify-items: center;
    align-items: center;
    text-align: center;
  }
  .hero__img--one {
    display: none;
  }
  .hero__img {
    display: block;
  }
  .hero__img div img {
    object-fit: contain;
    max-width: 100%;
  }
}

/* Small buttons  */
.btn__normal-small {
  font-size: 1.3em;
  font-weight: 800;
  color: #3aae84;
  background-color: #fff;
  padding: 0.7em 1.6em;
  border: 2px solid #fff;
  transition: ease;
  transition-duration: 80ms;
}

.btn__normal-small:hover {
  color: #fff;
  background-color: #3aae84;
  border: solid 2px #fff;
}
.atag-style {
  text-decoration: none;
}
.a_link_button:hover {
  background-color: #fff;
}
/* Normal button */
.btn__normal {
  font-size: 20pt;
  font-weight: 800;
  color: #3aae84;
  background-color: #fff;
  padding: 0.7em 1.6em;
  border: 2px solid #3aae84;
  transition: ease;
  transition-duration: 80ms;

  text-decoration: none;
}

.btn__normal:hover {
  color: #fff;
  background-color: #3aae84;
  border: solid 2px #fff;
}

.btn__outline {
  font-size: 14pt;
  color: #3aae84;
  padding: 0.7em 1.7em;
  background-color: white;
  border: solid 2px #3aae84;
  transition: ease;
  transition-duration: 80ms;
}

/* Button Outline black */

.btn__outline-black {
  font-size: 20pt;
  font-weight: 400;
  color: #000;
  padding: 0.7em 1.7em;
  background-color: white;
  border: 2px solid #000;
  transition: ease;
  transition-duration: 80ms;
}
.btn__outline-black:hover {
  background-color: #3aae84;
  color: #fff;

  border: 2px solid #fff;
}
/* Button outline green */
.btn__outline-green {
  font-size: 14pt;
  color: #3aae84;
  padding: 0.7em 1.7em;
  background-color: white;
  border: 2px solid #3aae84;
  transition: ease;
  transition-duration: 80ms;
}
.btn__outline-green:hover {
  background-color: #3aae84;
  color: #fff;

  border: 2px solid #fff;
}
@media screen and (min-width: 768px) {
  .btn__normal {
    background-color: #3aae84;
    color: #fff;
    border-color: #3aae84;
  }
  .btn__normal:hover {
    color: #3aae84;
    background-color: #fff;
    border: solid 2px #3aae84;
  }
}

.what-we-do {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 5rem;
}
.what-we-do__items {
  display: grid;
  grid-template-columns: repeat(
    auto-fit,
    minmax(18em, 1fr)
  );
  grid-gap: 1em;
  text-align: center;
  justify-content: center;
  align-items: center;
  grid-template-rows: repeat(
    auto-fit,
    minmax(10em, 1fr)
  );
}

.what-we-do_item {
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  margin: 2em;
}

